// // Images
.mypage-img-wrapper {
  width: 150px;
  height: 150px;
  overflow: hidden;
  border-radius: 20px;
}

.mypage-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mypage-img:after{
  background: linear-gradient(to top, #fff, #fff 10%, transparent);
}